.color {
    background-color: #45484e;
}

.changeMainPhoto {
    width: 350px;
    max-width: 100%;
    color: #fff;
    padding: 5px;
    background: #2f3136;
    border-radius: 10px;
    border: 1px solid #555;
}

.changeMainPhoto::file-selector-button {
    margin-right: 20px;
    border: none;
    background: #4676D7;
    padding: 10px 20px;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    transition: background .2s ease-in-out;
}

.changeMainPhoto::file-selector-button:hover {
    background: #1d49aa;
}

.dropContainer {
    position: relative;
    display: flex;
    gap: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 200px;
    padding: 20px;
    border-radius: 10px;
    border: 2px dashed #555;
    color: #fff;
    cursor: pointer;
    background: #45484e;
    transition: background .2s ease-in-out, border .2s ease-in-out;
}

.dropTitle {
    color: #fff;
    background: #45484e;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    transition: background .2s ease-in-out, border .2s ease-in-out;
}

.dropContainer:hover>.dropTitle {
    background: #2f3136;
    transition: .2s all ease
}

.dropContainer:hover {
    background: #2f3136;
    border-color: #eee;
    transition: .2s all ease;
}

.dropContainer:hover .drop-title {
    color: #fff;
}

.dragWrap {
    margin: 0;
    background-color: #45484e;
}

.myImage {
    width: max-content;
    background-color: #45484e;
    cursor: pointer;
    position: relative;
}

.guestImage {
    width: max-content;
    background-color: #45484e;
    position: relative;
}

.image:hover {
    background-color: #45484e;
}

.imageText {
    background-color: #45484e84;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    padding: 7px;
    border-radius: 10px;
    pointer-events: none;
}

.imageEdit {
    opacity: 0.4;
}