.color {
    background-color: #45484e;
}

.button {
    appearance: none;
    border: 0;
    border-radius: 5px;
    background: #4676D7;
    color: #fff;
    padding: 12px 24px;
    font-size: 16px;
    cursor: pointer;
    min-width: 120px;
    height: max-content;
    margin-top: 175px;
}

.button:hover {
    background: #1d49aa;
}

.button:focus {
    outline: none;
    box-shadow: 0 0 0 4px #cbd6ee;
}

.editButton {
    appearance: none;
    border: 0;
    border-radius: 5px;
    background: #4676D7;
    color: #fff;
    padding: 12px 24px;
    font-size: 16px;
    cursor: pointer;
    min-width: 120px;
    height: max-content;
}

.editButton:hover {
    background: #1d49aa;
}

.editButton:focus {
    outline: none;
    box-shadow: 0 0 0 4px #cbd6ee;
}

.textarea {
    color: #212529;
    width: 45%;
    height: 200px;
    background-color: #fff;
    border-color: #bdbdbd;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(158, 158, 158, 0.25);
    resize: none;
    border-radius: 5px;
    font-size: 20px;
}